import React from 'react'
import { Link, useParams } from 'react-router-dom'
import LazyImage from './UI/LazyImage'
import { useContext } from 'react'
import { BrowserContext } from '../store/BrowserContext'
import { TranslationContext } from '../store/TranslationContext'
import TimeElapsed from './TimeElapsed'

const ArticleCard2 = ({article, link, isInSavedArticles=false}) => {
    const {language} = useContext(BrowserContext)
    const {translate}=useContext(TranslationContext)
    return (
        <Link to={link} className={'col-12 col-md-6 col-xl-4 p-2' + (isInSavedArticles ? ' cursor-pointer':'')} style={{minWidth:300}} >
            <div className='card d-flex flex-row p-2 gap-2'>
                <button className='p-0 square overflow-hidden border rounded my-auto' style={{width: 100, height:100}}>
                    <LazyImage src={article.main_image} className={`mb-0`} />
                </button>
                <div className='d-flex flex-column flex-grow-1 px-2'>
                    <strong className='cut-text'>{article.brand_name} - { article.title }</strong>
                    <div className='cut-text'>
                        {article.price > 0 ? 
                            <span className='color-theme fw-bold'>{translate('Price')}: { article.price } {translate('Mi')}</span> : 
                            article.offered_price > 0 ? <span className='text-grey fw-bold'>{translate('Offered price')}: { article.offered_price } {translate('Mi')}</span > : 
                                <span className='text-danger'>{ translate('No price') }</span>
                        }
                    </div>
                    <div className='cut-text'><i className="fa-solid fa-location-dot fa-sm"></i> <span className=''>{article.state_name}{article.city ? ', ' + article.city:''}</span></div>
                    <div className='d-flex align-items-center gap-1 cut-text mt-1'>
                        <strong className='small'> {article.likes_count} </strong><i className=' fa-solid fa-thumbs-up fa-sm'/>
                        <strong className='small'> {article.views} </strong><i className="fa-solid fa-eye fa-sm"></i>
                    </div>
                    <TimeElapsed className='small color-grey margin-inline-start-auto' time={article.created_at} />
                </div>
            </div>
        </Link>
    )
}

export default ArticleCard2