import React from 'react'

const ArticleCard2Loading = ({addTop = false}) => {
  return (
    <div className={'col-12 col-md-6 col-xl-4 p-2'} style={{minWidth:300}}>
            <div className='card p-2'>
              { addTop && <><div className='p-3 m-1'></div><hr/></> }
              <div className='d-flex flex-row  gap-2'>
                  <img className='border rounded bg-color-grey' width={100} height={100} />
                  <div className='d-flex flex-column flex-grow-1 px-2 gap-1'>
                      <img className='border rounded bg-color-grey' width={'100%'} height={20}/>
                      <img className='border rounded bg-color-grey' width={'90%'} height={20}/>
                      <img className='border rounded bg-color-grey' width={'80%'} height={20}/>
                      <img className='border rounded bg-color-grey' width={'50%'} height={20}/>
                      <img className='margin-inline-start-auto border rounded bg-color-grey' width={'60%'} height={20}/>
                  </div>
              </div>
            </div>
              
        </div>
  )
}

export default ArticleCard2Loading